import React from 'react'
import {Link} from 'gatsby'
import styled from 'styled-components'
import img from '../images/karlanders_logo_blue.svg'

const Styled = styled.div`
  position: fixed;
  width: 200px;
  right: -190px;
  bottom: 0;
  margin-bottom: 25px;
  transform-origin: bottom left;
  transform: rotateZ(-90deg);
  z-index: 998;

  @media(min-width: 992px) {
    position: fixed;
    width: 300px;
    right: -280px;
    bottom: 0;
    margin-bottom: 25px;
    transform-origin: bottom left;
    transform: rotateZ(-90deg);
    z-index: 999;
  }

  img {
    height: 100%;
    width: 100%
  }
`
      

const Logo = (props) => (
    <Styled>
    <Link to={props.to}>
        <img alt="" src={img}/>
    </Link>
    </Styled>
)

export default Logo